<template>
  <div>
    <nav class="navbar navbar-expand-sm navbar-light fixed-top itdmnav d-block">
      <div>
        <div
          class="d-flex flex-nowrap justify-content-between align-items-center"
        >
          <div class="d-flex">
            <button class="btn" @click.prevent="backtrack">
              <i class="fa fa-arrow-left fa-lg" aria-hidden="true"></i>
            </button>
            <div class="minilogo"></div>
            <h5>
              <a class="navbar-brand" href="#">{{ currentuser.fullname }}</a>
            </h5>
          </div>
          <div>
            <button class="btn" @click.prevent="backlogin">
              回登入畫面<i
                class="fa fa-arrow-right fa-lg"
                aria-hidden="true"
              ></i>
            </button>
          </div>
        </div>
      </div>
    </nav>
    <main>
      <div class="content_wrapper" style="align-items: start">
        <div
          class="
            job_wrapper
            col col-sm-8 col-md-6 col-lg-3 col-xl-4
            container-fluid container-sm
          "
        >
          <form class="job_form col">
            <p class="error">{{ error }}</p>
            <qrcode-stream @decode="onDecode" @init="onInit" />
            <p class="decode-result">
              掃描結果: <b>{{ result }}</b>
            </p>
            <div
              class="error-message alert text-center"
              style="position: static; margin-bottom: 0"
              :class="{ hidden: forget }"
            >
              {{ hintmsg }}
            </div>
          </form>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
import { QrcodeStream } from "@/";
import { mapGetters, mapActions } from "vuex";
import getapilink from "@/assets/js/api/GetApiLink";
export default {
  components: { QrcodeStream },
  data() {
    return {
      result: "",
      error: "",
      username: { username: "" },
      hintmsg: "",
      forget: true,
    };
  },

  methods: {
    onDecode(result) {
      const vm = this;
      vm.result = result;
      switch (vm.result) {
        case "OverTimeOn": {
          const setoveronapi = getapilink + "ITDMLIMSAPI/DAC/SetOverTimeOn";
          this.$http
            .post(setoveronapi, vm.username)
            .then((response) => {
              if (response.data.overtime === 1) {
                vm.hintmsg = "加班開始";
                vm.forget = false;
              }
            })
            .catch((error) => {
              console.log(error);
            });
          break;
        }

        case "OverTimeOff": {
          const setoveroffapi = getapilink + "ITDMLIMSAPI/DAC/SetOverTimeOff";
          this.$http
            .post(setoveroffapi, vm.username)
            .then((response) => {
              if (response.data.overtime === 0) {
                vm.hintmsg = "加班結束";
                vm.forget = false;
              }
            })
            .catch((error) => {
              console.log(error);
            });
          break;
        }

        default: {
          const setdacapi = getapilink + "ITDMLIMSAPI/DAC/SetDAC";
          const userroomno = {
            username: vm.username.username,
            roomno: vm.result,
          };
          this.$http
            .post(setdacapi, userroomno)
            .then((response) => {
              vm.hintmsg = "掃描成功，進入" + response.data.roomno;
              vm.forget = false;
            })
            .catch((error) => {
              console.log(error);
              vm.hintmsg = "掃描失敗，請重掃描";
              vm.forget = false;
            });
          break;
        }
      }
    },
    async onInit(promise) {
      try {
        await promise;
      } catch (error) {
        if (error.name === "NotAllowedError") {
          this.error = "ERROR: you need to grant camera access permisson";
        } else if (error.name === "NotFoundError") {
          this.error = "ERROR: no camera on this device";
        } else if (error.name === "NotSupportedError") {
          this.error = "ERROR: secure context required (HTTPS, localhost)";
        } else if (error.name === "NotReadableError") {
          this.error = "ERROR: is the camera already in use?";
        } else if (error.name === "OverconstrainedError") {
          this.error = "ERROR: installed cameras are not suitable";
        } else if (error.name === "StreamApiNotSupportedError") {
          this.error = "ERROR: Stream API is not supported in this browser";
        }
      }
    },
    backtrack() {
      const vm = this;
      vm.$router.push("/SelMenu");
    },
    backlogin() {
      const vm = this;
      vm.$router.push("/");
    },
  },
  watch: {
    forget: function() {
      const vm = this;
      setTimeout(() => {
        vm.forget = true;
      }, 3000);
    },
  },
  computed: {
    ...mapGetters(["currentuser"]),
  },
  created() {
    const vm = this;
    vm.username.username = vm.currentuser.username;
  },
};
</script>

<style scoped>
.error {
  font-weight: bold;
  color: red;
}
</style>
